import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: '/' ,
    name: 'index' ,
    component: () => import("@/views/index.vue")
  } ,
  {
    path: '/secAuthen' ,
    name: 'secAuthen' ,
    component: () => import("@/views/platManage/secAuthen.vue")
  } ,
  {
    path: '/state' ,
    name: 'state' ,
    component: () => import("@/views/platManage/state.vue")
  } ,
  {
    path: '/info' ,
    name: 'info' ,
    component: () => import("@/views/platManage/info.vue")
  } ,
  {
    path: '/staAna' ,
    name: 'staAna' ,
    component: () => import("@/views/platManage/staAna.vue")
  } ,
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
