<template>
  <div id="Aside">
    <div class="row" v-for="(item, index) in navArray" :key="index" @click="change(index, item)">
      <div class="frist_row bbox ycenter pointer">
        <img class="img" src="@/assets/img/add.png" />
        <span>{{item.title}}</span>
        <span v-if="item.showChild">1</span>
        <span v-else>2</span>
      </div>
        <div
          v-show="item.showChild"
          :class="{active:item2.status}"
          class="second_row bbox ycenter pointer"
          v-for="(item2, index2) in item.child"
          :key="index2"
          @click.stop="checkItem(index, index2)"
        >
          <span>{{item.child[index2].title}}</span><span>{{index2}}</span>
        </div>
    </div>
  </div>
</template>
<script>
let bIndex = { index: null, index2: null };
export default {
  data() {
    return {
      navArray: [
        {
          title: "区块链管理",
          icon: "el-icon-edit",
          showChild: false,
          child: [
            {
              title: "区块链基本信息",
              status: false,
              link: "info"
            },
            {
              title: "状态检测",
              status: false,
              link: "state"
            }]
        }
      ]
    };
  },

  methods: {
    change(index, item) {
      let indexObject = this.navArray[index];
      if (item.child) {
        if (index !== bIndex.index && bIndex.index != null) {
          this.navArray[bIndex.index].showChild = false;
          this.navArray[index].showChild = true;
          if (bIndex.index2 !== null) {
            this.navArray[bIndex.index].child[bIndex.index2].status = false;
          }
        } else {
          this.navArray[index].showChild = !this.navArray[index].showChild;
        }
      } else {
        this.navArray[bIndex.index].showChild = false;
        this.navArray[index].showChild = true;
      }
      bIndex.index = index;
    },

    checkItem(index, index2) {
      if (index === bIndex.index) {
        ///同一个一级标题下
        if (!this.navArray[index].child[index2].status) {
          if (bIndex.index2 !== null) {
            this.navArray[bIndex.index].child[bIndex.index2].status = false;
          }
          this.navArray[index].child[index2].status = true;
        }
      } else if (index !== bIndex.index) {
        if (bIndex.index2 !== null) {
          this.navArray[bIndex.index].child[bIndex.index2].statu = false;
          this.navArray[index].child[index2].statu = true;
        }
        this.navArray[bIndex.index].showChild = false;
        this.navArray[index].showChild = true;
      }
      bIndex.index = index;
      bIndex.index2 = index2;
      this.$router.push({ name: this.navArray[index].child[index2].link });
    }
  }
};

</script>

<style lang="less" scoped>
#Aside {
  .row {
    line-height: 50px;
    width: 100%;
    background: 80px;
    box-sizing: border-box;
    .frist_row:hover {
      background: rgba(255, 255, 255, 0.1);
    }
    .second_row:hover {
      background: rgba(255, 255, 255, 0.1);
    }
    .frist_row {
      height: 50px;
      width: 100%;
      font-family: MicrosoftYaHeiLight;
      font-size: 18px;
      font-stretch: normal;
      letter-spacing: 2px;
      color: #fefefe;
      .img {
        margin-left: 20px;
        margin-right: 10px;
        width: 20px;
        height: 20px;
      }
    }
    .second_row {
      height: 50px;
      width: 100%;
      padding-left: 60px;
      box-sizing: border-box;
    }
  }
}

.transitionName-enter-active {
    transition: all 0.3s ease-in;
}
</style>